body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  text-align: center;
  font-family: 'Signika', sans-serif; }

h1, h2 {
  color: #8899ab; }

p {
  font-size: 2rem;
  font-weight: 300; }

.block {
  margin-top: 6rem; }

.title {
  font-size: 6rem; }

.light {
  font-weight: 300; }

.italic {
  font-style: italic; }

.paragraph {
  margin: 12rem; }

.big-picture {
  width: 100%; }

.header {
  min-height: 10vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  border-bottom: 2px solid #8899ab; }

.left-links {
  position: absolute;
  left: 10px;
  bottom: 0px; }

a {
  font-size: 1rem;
  font-weight: 300;
  margin: 10px;
  text-decoration: none; }

.favicon {
  height: 40px;
  position: absolute;
  bottom: -3px;
  margin-left: -20px; }

.intro {
  margin: 8rem; }

.social-media img {
  height: 1.5rem;
  margin: 0rem 3rem; }

.projects {
  padding: 0;
  overflow: hidden; }

.project img {
  width: 33.34vw;
  margin-left: -10px; }

.footer {
  margin-top: 20vh;
  padding: 5rem;
  color: #fff;
  background: #8899ab; }
  .footer p {
    font-size: 1.5rem; }

h1 {
  font-size: 3rem; }

h3 {
  font-size: 2.5rem;
  margin-bottom: 4rem; }

